<template>
  <div class="questions-container v-section container">
    <!-- <p>{{ locale }}</p> -->
    <div class="col-md-12">
      <div
        class="progress-circle-bar"
        :style="{
          background: `conic-gradient(#a70532 ${progressCircleVal}%, white 0)`,
        }"
      ></div>
    </div>
    <img :src="bgImages" />
    <div class="row">
      <div class="col-md-7">
        <h2>{{ question }}</h2>
      </div>
      <Question
        :question="question"
        :answers="answers"
        :progressCircleVal="progressCircleVal"
        :redirectToProducts="hasNextStepProducts"
        @send="checkAnswer"
      >
      </Question>
    </div>
    <nav v-if="renderButtons" class="back-btn-wrapper col-md-7">
      <button @click.prevent="previous()" class="btn back-btn" role="button">
        <img :src="require(`@/assets/icons/back-icon.png`)" />
      </button>
      <!-- <button
        @click.prevent="restartQuestions($event)"
        class="btn btn-info"
        role="button"
      >
        Restart Consultation
      </button> -->
    </nav>
  </div>
</template>

<script>
import { JSONPath } from 'jsonpath-plus';
import get from 'lodash.get';
import Question from './Question.vue';
import trackingEvents from '../tracking';

export default {
  name: 'QuestionsView',
  components: {
    Question,
  },
  data() {
    const answers = [];
    const data = {};
    const id = 0;
    const hasNextStepProducts = false;
    const selectedProducts = [];
    const question = '';
    const bgImages = '';
    const progressCircleVal = Number;

    return {
      id,
      data,
      answers,
      bgImages,
      hasNextStepProducts,
      progressCircleVal,
      question,
      selectedProducts,
      renderButtons: false,
    };
  },
  computed: {
    locale() {
      return this.$store.state.locale;
    },
  },
  mounted() {
    trackingEvents.emitMtmEvent({
      // TODO right tracking value for page
      event: 'app.page',
      'app.page': this.$options.name,
    });
    this.$store.commit('resetAnswerList');
    this.checkAnswer(this.id, false);
    const questions = this.$store.getters.questionsFile;
    this.loadData(questions);
  },
  methods: {
    loadData(questions) {
      const pathToData = `data/${questions}`;
      fetch(pathToData)
        .then((response) => response.json())
        .then((data) => this.initData(data))
        .catch((e) => {
          window.console.log('Error fetching question json', e);
        });
    },
    initData(data) {
      this.data = data;
      this.id = this.getRootId();
      this.question = data.QuestionText;
      this.answers = data.Answers;
      this.bgImages = data.BackgroundImage;
      this.progressCircleVal = data.Progress;
    },
    checkAnswer(id, redirection) {
      this.$store.commit('addToAnswerList', id);
      if (redirection) {
        const result = this.queryData(id);
        this.$store.commit('addToProductList', result[0].NextStep);
        this.$router.push({ path: '/products' });
        return;
      }
      this.setData(id);
    },
    setData(id) {
      this.id = id;
      const result = this.queryData(id);
      if (!result) {
        return;
      }

      const hasNextQuestion = get(result[0], 'NextStep.Answers', false);
      this.hasNextStepProducts = !Array.isArray(
        hasNextQuestion[0].NextStep.Answers
      );
      if (hasNextQuestion) {
        this.renderButtons = true;
        this.question = result[0].NextStep.QuestionText;
        this.answers = result[0].NextStep.Answers;
        this.bgImages = result[0].NextStep.BackgroundImage;
        this.progressCircleVal = result[0].NextStep.Progress;
      }
    },
    queryData(id) {
      return JSONPath({
        path: `$..Answers[?(@.Id=="${id}")]`,
        json: this.data,
        wrap: false,
      });
    },
    previous() {
      const lastQuestion = this.$store.getters.lastQuestionId;
      if (!lastQuestion) {
        this.restartQuestions();
        return;
      }
      this.setData(lastQuestion);
    },
    restartQuestions() {
      this.question = this.data.QuestionText;
      this.answers = this.data.Answers;
      this.bgImages = this.data.BackgroundImage;
      this.progressCircleVal = this.data.Progress;
      this.renderButtons = false;
      this.id = this.getRootId();
      this.$store.commit('resetAnswerList');
    },
    getRootId() {
      const rootId = JSONPath({
        path: "$.Answers[0]['Id']",
        json: this.data,
        wrap: false,
      });
      return rootId || 0;
    },
  },
};
</script>

<style lang="scss" scoped>
.questions-container {
  margin-bottom: 40px;

  img {
    left: 0;
    position: absolute;
  }

  nav {
    text-align: center;
  }
}
.questions-container .row {
  justify-content: flex-end;
  padding-bottom: 60px;
  padding-top: 100px;
}
.questions-container h2 {
  color: #636363;
  margin-bottom: 40px;
  font-size: 38px;
  text-transform: uppercase;
  font-weight: 400;
  margin-left: -15px;
  font-family: 'eucerinawgl-regular-webfont';
}
.btn {
  padding: 0;
  + .btn {
    margin-left: 25px;
  }
}

.back-btn {
  height: 64px;
  left: -64px;
  width: 64px;
}

.back-btn:before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
}
.progress-circle-bar {
  position: absolute;
  width: 35px;
  height: 35px;
  border: 2px solid #878c92;
  display: flex;
  align-content: center;
  align-items: center;
  align-self: center;
  justify-content: center;
  justify-items: center;
  justify-self: center;
  border-radius: 50%;
  place-items: center;
  top: 35px;
  right: -85px;
  z-index: 9999;

  &:before {
    content: '';
    position: absolute;
    width: calc(100% - 12px);
    border: 2px solid #878c92;
    height: calc(100% - 12px);
    background: white;
    border-radius: 50%;
  }
}
</style>
