 <template>
  <div class="panel col-md-12">
    <div class="row question-item">
      <!-- <div class="col-md-5">
        <h2 class="">
          {{ question }}
        </h2>
      </div> -->
      <ul
        class="col-md-7 list-group"
        v-bind:class="answers[0].Icon ? 'two-cols' : 'one-col'"
      >
        <li
          v-for="(answer, i) in answers"
          :key="i"
          @click="checkProductsRoute(answer.Id, $event)"
          class="list-group-item"
        >
          <span class="product-icon" v-if="answer.Icon">
            <img :src="answer.Icon" />
          </span>
          <span class="btn btn-light">{{ answer.AnswerText }}</span>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Question',
  components: {},
  data() {
    console.log('answers', this.answers);
    return {
      isActive: true,
      errorClass: 'no-icon',
      console,
    };
  },
  props: {
    id: {
      type: [Number, String],
      default: 0,
    },
    question: {
      type: String,
      default: '',
    },
    answers: {
      type: Array,
      default: () => [],
    },
    redirectToProducts: {
      type: Boolean,
      default: false,
    },
  },
  methods: {
    checkProductsRoute(id, event) {
      if (this.redirectToProducts) {
        event.preventDefault();
        this.$emit('send', id, true);
        return;
      }

      this.$emit('send', id, false);
    },
  },
};
</script>
