.list-group-item {
  background-image: linear-gradient(380deg, #ffffff 0%, #d7e1ec 280%);
  box-shadow: 3px 3px 14px 1px #d7d7d7;
  cursor: pointer;
  position: relative;
  padding: 0;
  text-align: center;
  margin-bottom: 10px;
  font-family: 'Roboto-Regular';

  span {
    display: block;
    padding-top: 25px;
  }
  &:after {
    content: '';
    background-color: transparent;
    left: 0;
    height: 100%;
    position: absolute;
    z-index: 1;
    top: 0;
    width: 10px;
  }

  &:not(:first-child) {
    border-top: 1px solid rgba(0, 0, 0, 0.125);
  }

  &:hover {
    color: #a70532;

    &:after {
      background-color: #a70532;
      border-radius: 10px;
      transition: 0.2s all ease;
    }
  }
}

.question-item {
  align-items: center;
  justify-content: flex-end;

  .two-cols {
    flex-flow: wrap;
    justify-content: space-between;

    .list-group-item {
      display: flex;
      flex-direction: column;
      min-height: 207px;
      justify-content: flex-end;
      width: 48%;
    }
  }
}

.question-item .btn-light {
  background: transparent;
  border: none;
  color: #a70532;
  font-size: 32px;
  padding-bottom: 9px;
  padding-top: 9px;
  text-transform: uppercase;
}

.question-item .list-group-item,
.question-item .btn-light {
  border-radius: 10px;
}
</style>
